// 
import {CertType} from '@/util/diamond_util';
import {ChainBase} from '@/model/chainbase_model';

export class Cert extends ChainBase {
    public map = new Array<{ key: string, value: string }>(); // maparray
    public i18n = false; // key
    public certType = CertType.None;

    public getId(): string {
        if (!this.id) {
            let index = this.map.findIndex((it) => {
                return it.key === 'gia';
            });

            if (index < 0) {
                index = this.map.findIndex((it) => {
                    return it.key === 'id';
                });
            }
            if (index >= 0) {
                this.id = this.map[index].value;
            }
        }
        return this.id;
    }
}

// gia
export class CertGia extends ChainBase {
    public gia = '';        // 
    public carat = '';      // 
    public color = '';      // 
    public clarity = '';    // 
    public cut = '';        // 
    public hash = '';       // uuid

    public toCert(): Cert {
        const cert = new Cert();
        cert.i18n = true; // 
        cert.certType = CertType.Gia;
        Object.assign(cert, this as ChainBase);
        if (this.gia && this.gia !== "") {
            cert.map.push({key: 'gia', value: this.gia});
        }
        if (this.carat && this.carat !== "") {
            cert.map.push({key: 'carat', value: this.carat});
        }
        if (this.color && this.color !== "") {
            cert.map.push({key: 'color', value: this.color});
        }
        if (this.clarity && this.clarity !== "") {
            cert.map.push({key: 'clarity', value: this.clarity});
        }
        if (this.cut && this.cut !== "") {
            cert.map.push({key: 'cut', value: this.cut});
        }
        if (this.hash && this.hash !== "") {
            cert.map.push({key: 'hash', value: this.hash});
        }

        return cert;
    }

    public toGia(cert: Cert): boolean {
        // todo key  Gia gia 
        if (cert.certType === CertType.Gia) {
            Object.assign(this, cert as ChainBase);
            this.gia = cert.map[0].value;
            this.carat = cert.map[1].value;
            this.color = cert.map[2].value;
            this.clarity = cert.map[3].value;
            this.cut = cert.map[4].value;
            this.hash = cert.map[4].value;
        }
        return false;
    }
}

//
export enum UpChainStatus {
    Success,
    Failure,
    None
}

//
export enum CertSignStatus {
    Success,
    Failure,
    None
}

//cert
export class AttachInfo extends ChainBase {
    public inputData = '';                          // 19
    public businessAddress = '';                    // Address 
    public upChainStatus = UpChainStatus.None;      // 
    public certSignStatus = CertSignStatus.None;    // 
    public certSignedInfo = "";                       // 
}

export class CertOwn {
    public certGia = new CertGia();
    public attachInfo = new AttachInfo();


    //"{\"gia\":\"6666666\",\"carat\":\"1.58\",\"color\":\"H\",\"clarity\":\"VS2\",\"cut\":\"Excellent\",\"hash\":\"0x287e7ae36b946368a91ec89bf7bebe6968caa3a799b0daa4ca9742d208315f52\",\"msg\":\"test msg\"}"
    public assembleJsonInfo(): string {
        return `{"gia":"` + this.certGia.gia + `","carat":"` + this.certGia.carat + `","color":"`
            + this.certGia.color + `","clarity":"` + this.certGia.clarity + `","cut":"` + this.certGia.cut
            + `","hash":"` + this.certGia.hash + `","msg":"` + this.attachInfo.inputData + `"}`;
    }

    public hasOwn(): boolean {
        if (this.attachInfo != null && this.attachInfo.txid != null) {
            return true;
        } else {
            return false;
        }
    }
}
